export function damageString(skill) {
	if ("altDamage" in skill) return skill.altDamage;
	else if (skill.color === "purple") return "".repeat(skill.damage);
	else if (skill.color === "white" || skill.color === "gold") return "" + skill.damage;
	else return "";
}

export function getColorCode(color) {
	switch (color) {
		case "miss":
			return "#ea5050";
		case "blue":
			return "#56c4f2";
		case "purple":
			return "#c970e6";
		case "gold":
			return "#ead24c";
		case "white":
			return "#f9f9f9";
		default:
			return "#072e55";
	}
}

export function getDarkColorCode(color) {
	switch (color) {
		case "miss":
			return "#e44e4e";
		case "blue":
			return "#54beeb";
		case "purple":
			return "#c36de0";
		case "gold":
			return "#e4cc49";
		case "white":
			return "#f2f2f2";
		default:
			return "#072e55";
	}
}

export function intRandom(min, max) {
	return Math.floor(Math.random() * (max - min + 1)) + min;
}

export function typeTranslate(name) {
	const typeNames = [
		{ en: "Normal", ja: "" },
		{ en: "Fire", ja: "" },
		{ en: "Water", ja: "" },
		{ en: "Electric", ja: "" },
		{ en: "Grass", ja: "" },
		{ en: "Ice", ja: "" },
		{ en: "Psychic", ja: "" },
		{ en: "Fighting", ja: "" },
		{ en: "Poison", ja: "" },
		{ en: "Ground", ja: "" },
		{ en: "Flying", ja: "" },
		{ en: "Bug", ja: "" },
		{ en: "Rock", ja: "" },
		{ en: "Ghost", ja: "" },
		{ en: "Dragon", ja: "" },
		{ en: "Dark", ja: "" },
		{ en: "Steel", ja: "" },
		{ en: "Fairy", ja: "" },
	];
	for (let { en, ja } of typeNames) {
		if (en === name) return ja;
		if (ja === name) return en;
	}
	return null;
}
