import { Link } from "react-router-dom";
import "./QuizHome.css";
import figureMaster from "./../figure.json";
import { intRandom } from "../Components/Utility";

function QuizHome({ difficulty, setDifficulty, setProblemList }) {
	const problemSize = 10;

	const handleQuizStart = () => {
		let tmp = [];
		for (let i = 0; i < problemSize; i++) {
			while (true) {
				let randomval = intRandom(0, figureMaster.length);
				if (!tmp.includes(randomval)) {
					tmp.push(randomval);
					break;
				}
			}
		}
		setProblemList(tmp);
	};

	return (
		<div className="home-all">
			<label className="home-select-difficulty">
				
				<select
					name="selectDifficulty"
					value={difficulty}
					onChange={(e) => setDifficulty(parseInt(e.target.value))}
				>
					<option value="0">Easy</option>
					<option value="1">Normal</option>
					<option value="2">Hard</option>
					<option value="3">Extream</option>
				</select>
			</label>

			<Link to={"/quiz"} className="home-start-btn" onClick={handleQuizStart}>
				
			</Link>

			<Link to={"/library"} className="home-start-btn">
				
			</Link>
		</div>
	);
}

export default QuizHome;
