import "./Library.css";
import Roulette from "../Components/Roulette";
import figureMaster from "../figure.json";
import { useState } from "react";
import DetailModal from "../Components/DetailModal";
import { FixedSizeGrid } from "react-window";
import { Link } from "react-router-dom";

function Library() {
	const [detailIndex, setDetailIndex] = useState(null);

	const rowSize = 5;

	const Cell = ({ columnIndex, rowIndex, style }) => {
		const figureIndex = columnIndex + rowIndex * rowSize;

		if (figureIndex >= figureMaster.length) return <></>;

		const figure = figureMaster[figureIndex];

		return (
			<div className="library-element" style={style} key={`library-${figure.name}`}>
				<Roulette
					className="library-figure-roulette"
					data={figure.skills}
					pos_x={100}
					pos_y={100}
					radius={70}
					onClick={() => setDetailIndex(figureIndex)}
				></Roulette>
				<p className="library-figure-name">{figure.name}</p>
			</div>
		);
	};

	const cellWidth = Math.max(250, window.innerWidth / rowSize);

	return (
		<div className="library-all">
			<DetailModal
				isOpen={detailIndex !== null}
				onClose={() => setDetailIndex(null)}
				figure={figureMaster[detailIndex]}
				angle={0}
				onRouletteClick={() => {}}
			/>

			<div style={{ overflow: "hidden" }}>
				<FixedSizeGrid
					columnCount={rowSize}
					height={window.innerHeight}
					rowCount={Math.ceil(figureMaster.length / rowSize)}
					rowHeight={window.innerHeight / 2.5}
					columnWidth={cellWidth}
					width={window.innerWidth}
				>
					{Cell}
				</FixedSizeGrid>
			</div>

			<Link to={"/"}></Link>
		</div>
	);
}

export default Library;
