import "./DisplaySkill.css";
import * as utility from "./../Components/Utility.jsx";

function DisplaySkill({ skills, className = "" }) {
	return (
		<table className={`${className}`}>
			<thead>
				<tr className="displayskill-caption">
					<th></th>
					<th></th>
					<th></th>
				</tr>
			</thead>

			<tbody>
				{skills.map((skill) => {
					return (
						<>
							<tr
								className="displayskill-skill"
								style={{
									backgroundColor: utility.getColorCode(skill.color),
									borderLeft: "none",
									borderRight: "none",
								}}
							>
								<td className="displayskill-range">{skill.range}</td>
								<td
									className="displayskill-name"
									// style={{ borderRightColor: utility.getColorCode(skill.color) }}
								>
									{skill.name}
								</td>
								<td
									className="displayskill-damage"
									// style={{ borderLeftColor: utility.getColorCode(skill.color) }}
								>
									{utility.damageString(skill)}
								</td>
							</tr>

							{skill.detail !== "" && (
								<tr>
									<td className="displayskill-detail" colSpan={3}>
										{skill.detail}
									</td>
								</tr>
							)}
						</>
					);
				})}
			</tbody>
		</table>
	);
}

export default DisplaySkill;
