import "./Result.css";
import figureMaster from "./../figure.json";
import Roulette from "../Components/Roulette";
import { useState } from "react";
import { TwitterIcon, TwitterShareButton } from "react-share";
import { Link } from "react-router-dom";
import DetailModal from "../Components/DetailModal";

function Result({ problemList, answerList, difficulty }) {
	const [detailFigure, setDetailFigure] = useState(null);

	const isCorrect = problemList.map((prob, idx) => answerList[idx] === figureMaster[prob].name);

	const difficultyName = (() => {
		switch (difficulty) {
			case 0:
				return "Easy";
			case 1:
				return "Normal";
			case 2:
				return "Hard";
			case 3:
				return "Extream";
			default:
				return "difficulty-error";
		}
	})();

	return (
		<>
			<div className="result-header">
				<p>{isCorrect.filter((e) => e).length}</p>
				{problemList.map((probNum, idx) => {
					return (
						<p key={probNum} className={"result-resultcolor-" + (isCorrect[idx] ? "yellow" : "red")}>
							{isCorrect[idx] ? "" : ""}
						</p>
					);
				})}

				<TwitterShareButton
					url="https://skdiary.github.io/RouletteQuiz"
					title={` [${difficultyName}]10${
						isCorrect.filter((e) => e).length
					}`}
				>
					<TwitterIcon size={44} round />
				</TwitterShareButton>
			</div>

			<DetailModal
				isOpen={detailFigure !== null}
				onClose={() => setDetailFigure(null)}
				figure={figureMaster[problemList[detailFigure]]}
			/>

			<div className="result-list-all">
				{problemList.map((problemFigure, problemIndex) => {
					return (
						<div key={`result-list-${problemFigure}`} className="result-list-element">
							<div style={{ display: "flex", justifyContent: "center", gap: "10px" }}>
								<p style={{ fontSize: "x-large" }}>{problemIndex + 1}</p>
								<p className={"result-resultcolor-" + (isCorrect[problemIndex] ? "yellow" : "red")}>
									{isCorrect[problemIndex] ? "" : ""}
								</p>
							</div>
							<div className="result-list-flex">
								<Roulette
									data={figureMaster[problemFigure].skills}
									pos_x={90}
									pos_y={80}
									radius={80}
								></Roulette>
								<div>
									<p>{answerList[problemIndex]}</p>
									{!isCorrect[problemIndex] && <p>{figureMaster[problemFigure].name}</p>}

									<button
										onClick={() => {
											setDetailFigure(problemIndex);
										}}
									>
										
									</button>
								</div>
							</div>
						</div>
					);
				})}
			</div>

			<Link to={"/"} className="home-start-btn">
				
			</Link>
		</>
	);
}

export default Result;
