import "./QuizMain.css";
import figureMaster from "./../figure.json";
import { useEffect, useState } from "react";
import Roulette from "./../Components/Roulette.jsx";
import DisplaySkill from "./../Components/DisplaySkill.jsx";
import { useNavigate } from "react-router-dom";
import { typeTranslate } from "../Components/Utility";

function QuizMain({ problemList, answerList, setAnswerList, difficulty }) {
	const [problemIndex, setProblemIndex] = useState(0);
	const [thisAnswer, setThisAnswer] = useState("");
	const problemSize = problemList.length;

	const navigate = useNavigate();

	const hiddenSkills = (() => {
		let tmpSkills = [...figureMaster[problemList[problemIndex]].skills].map((e) => ({ ...e }));
		if (difficulty === 3) {
			for (let i = 0; i < tmpSkills.length; i++) {
				tmpSkills[i].name = "????";
				tmpSkills[i].detail = "";
			}
		}
		return tmpSkills;
	})();

	useEffect(() => {
		setAnswerList(Array(problemSize).fill(""));
	}, [problemSize]);

	const nextProblem = (e) => {
		e.preventDefault();

		let tmp = [...answerList];
		tmp[problemIndex] = thisAnswer;
		setAnswerList(tmp);

		// setAnswerList([...answerList, thisAnswer]);
		setThisAnswer("");

		if (problemIndex + 1 === problemSize) {
			navigate("/result");
		} else {
			setProblemIndex(problemIndex + 1);
		}
	};

	const hideIfDifficult = (border, text, length = 4) => (difficulty < border ? text : "?".repeat(length));

	return problemList.length === 0 ? (
		<p></p>
	) : (
		<>
			{/*  */}
			<div className="quizmain-header">
				<p>{problemIndex + 1}</p>

				{/* <p></p> */}
				<div className="quizmain-result-items">
					{problemList.map((probNum, idx) => {
						const correct =
							idx < problemIndex ? (answerList[idx] === figureMaster[probNum].name ? 1 : 0) : -1;
						return (
							<p
								key={probNum}
								className={
									"quizmain-resultcolor-" + (correct === -1 ? "white" : correct ? "yellow" : "blue")
								}
							>
								{correct === -1 ? "" : correct ? "" : ""}
							</p>
						);
					})}
				</div>
			</div>

			{/*  */}
			{problemList.length !== 0 && (
				<>
					<div className="quizmain-problem">
						<Roulette
							className="quizmain-roulette"
							data={hiddenSkills}
							pos_x={100}
							pos_y={100}
							radius={100}
							mp={hideIfDifficult(2, figureMaster[problemList[problemIndex]].mp, 1)}
							rarity={hideIfDifficult(2, figureMaster[problemList[problemIndex]].rarity, 2)}
						/>

						<div className="quizmain-figure-detail">
							<p>
								
								{hideIfDifficult(
									1,
									figureMaster[problemList[problemIndex]].types
										.map((e) => typeTranslate(e))
										.join(", ")
								)}
								<br />
								{/* {hideIfDifficult(2, figureMaster[problemList[problemIndex]].rarity)}
								<br />
								MP : {hideIfDifficult(2, figureMaster[problemList[problemIndex]].mp)}
								<br /> */}
								{hideIfDifficult(2, figureMaster[problemList[problemIndex]].ability)}
								<br />
								{hideIfDifficult(2, figureMaster[problemList[problemIndex]].abilityDetail, 0)}
								<br />
							</p>
						</div>
					</div>

					<form className="quizmain-answer-form" onSubmit={nextProblem}>
						<label>
							
							<input
								name="answer"
								value={thisAnswer}
								onChange={(event) => setThisAnswer(event.target.value)}
							/>
						</label>
						<button type="submit" className="quizmain-answer-submit">
							
						</button>
					</form>

					<DisplaySkill className="quizmain-displayskill" skills={hiddenSkills} />
				</>
			)}
		</>
	);
}

export default QuizMain;
